'use strict';

/**
* Compute the sum of double-precision complex floating-point strided array elements using an improved KahanBabuka algorithm.
*
* @module @stdlib/blas-ext-base-zsumkbn
*
* @example
* import Complex128Array from '@stdlib/array-complex128';
* import zsumkbn from '@stdlib/blas-ext-base-zsumkbn';
*
* var x = new Complex128Array( [ 1.0, -2.0, 2.0, 3.0 ] );
*
* var v = zsumkbn( x.length, x, 1 );
* // returns <Complex128>[ 3.0, 1.0 ]
*
* @example
* import Complex128Array from '@stdlib/array-complex128';
* import zsumkbn from '@stdlib/blas-ext-base-zsumkbn';
*
* var x = new Complex128Array( [ 1.0, -2.0, 2.0, 3.0 ] );
*
* var v = zsumkbn.ndarray( x.length, x, 1, 0 );
* // returns <Complex128>[ 3.0, 1.0 ]
*/

// MODULES //

var join = require( 'path' ).join;
import tryRequire from '@stdlib/utils-try-require';
import isError from '@stdlib/assert-is-error';
import main from './main.js';


// MAIN //

var zsumkbn;
var tmp = tryRequire( join( __dirname, './native.js' ) );
if ( isError( tmp ) ) {
	zsumkbn = main;
} else {
	zsumkbn = tmp;
}


// EXPORTS //

export default zsumkbn;

// exports: { "ndarray": "zsumkbn.ndarray" }
