'use strict';

/**
* Compute the sum of single-precision complex floating-point strided array elements.
*
* @module @stdlib/blas-ext-base-csum
*
* @example
* import Complex64Array from '@stdlib/array-complex64';
* import csum from '@stdlib/blas-ext-base-csum';
*
* var x = new Complex64Array( [ 1.0, -2.0, 2.0, 3.0 ] );
*
* var v = csum( x.length, x, 1 );
* // returns <Complex64>[ 3.0, 1.0 ]
*
* @example
* import Complex64Array from '@stdlib/array-complex64';
* import csum from '@stdlib/blas-ext-base-csum';
*
* var x = new Complex64Array( [ 1.0, -2.0, 2.0, 3.0 ] );
*
* var v = csum.ndarray( x.length, x, 1, 0 );
* // returns <Complex64>[ 3.0, 1.0 ]
*/

// MODULES //

var join = require( 'path' ).join;
import tryRequire from '@stdlib/utils-try-require';
import isError from '@stdlib/assert-is-error';
import main from './main.js';


// MAIN //

var csum;
var tmp = tryRequire( join( __dirname, './native.js' ) );
if ( isError( tmp ) ) {
	csum = main;
} else {
	csum = tmp;
}


// EXPORTS //

export default csum;

// exports: { "ndarray": "csum.ndarray" }
